@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Raleway:wght@100;400;600&family=Roboto+Mono:wght@400;600&display=swap);
html,body,h1,h2,h3,h4,h5,h6,div,p,ul,ol{margin:0;padding:0}html{font-family:"Raleway",sans-serif;color:#fff;scroll-behavior:smooth}html,body{background-color:#333337}a{color:#fff;transition:color .1s}a:hover{color:#ccc}a:active{color:#bbb}
.Header{position:fixed;z-index:100;width:100%;box-sizing:border-box;display:flex;justify-content:space-between;padding:20px 6vw;background-color:rgba(34,34,36,.3);border-bottom:1px solid rgba(255,255,255,.1);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px)}.Header .logo-area{display:block;margin:auto 0}.Header .logo-area>*{display:inline-block;vertical-align:middle}.Header .logo-area p{font-size:24px;font-weight:100;transform:translateY(-1px)}.Header .text-logo{font-size:36px;transform:scaleX(1.15);font-family:"Bebas Neue",sans-serif}.Header .contact{transform:translateY(2px)}.Header .contact a{display:inline-block;font-family:"Roboto Mono",monospace;vertical-align:middle;text-decoration:none}.Header .contact a.linkedIn{margin-right:15px;transform:translateY(3px)}@media screen and (max-width: 640px){.Header .logo-area p,.Header .logo-area .inline-bar{display:none}.Header .text-logo{display:block}}
