.ScrollToTopButton{position:fixed;display:block;right:29px;bottom:29px;cursor:pointer;z-index:5;-webkit-backdrop-filter:blur(15px);backdrop-filter:blur(15px);width:45px;height:45px;border:3px solid #fff}.ScrollToTopButton:before{content:" ";position:absolute;bottom:0;left:0;width:100%;height:0;background-color:rgba(255,255,255,.5);transition:.4s cubic-bezier(0.2, 0.61, 0.41, 0.84);transition-property:height,background-color}.ScrollToTopButton:hover:before{height:100%}.ScrollToTopButton:active{color:rgba(255,255,255,.5);border-color:rgba(255,255,255,.5)}.ScrollToTopButton:active:before{background-color:rgba(255,255,255,.25)}.ScrollToTopButton:focus{outline:none}.ScrollToTopButton>.arrow-icon{display:block;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);font-size:44px}
.FrontPage{background-image:url(/portfolio/static/media/stars-background-v2.34b9175c.jpg);background-repeat:no-repeat;background-size:cover;padding-top:88px;padding-bottom:60px}.FrontPage .featured{margin-top:200px;display:flex;justify-content:center}.FrontPage .featured>*{flex:0 0 50%;max-width:315px;margin:0 auto}.FrontPage .bio .key-words{margin-bottom:10px;font-family:"Roboto Mono",monospace;font-weight:700;list-style-type:none;display:flex;justify-content:space-between}.FrontPage .bio .key-words>*:not(:first-child){margin-left:15px}.FrontPage .bio .desc{margin-bottom:10px;line-height:140%}.FrontPage .projects-pointer{display:table;margin:0 auto;text-align:center;margin-top:200px;cursor:pointer}.FrontPage .projects-pointer *{transition:color .1s}.FrontPage .projects-pointer:hover *{color:#ccc}.FrontPage .projects-pointer:active *{color:#bbb}.FrontPage .projects-pointer .pointer-title{display:table;margin:0 auto;text-align:center;font-size:56px}.FrontPage .projects-pointer .arrow-icon{margin-top:-30px;font-size:100px;animation-name:UpAndDown;animation-duration:.75s;animation-direction:alternate;animation-iteration-count:infinite;animation-timing-function:ease-in}@keyframes UpAndDown{0%{transform:translateY(-7px)}100%{transform:translateY(7px)}}@media screen and (max-width: 640px){.FrontPage .featured{margin-top:100px;display:flex;justify-content:center}.FrontPage .featured>*{flex:0 0 100%}.FrontPage .featured>*:not(:first-child){display:none}.FrontPage .bio .bio-title{text-align:center}.FrontPage .bio .key-words{font-family:"Roboto Mono",monospace;font-weight:700;list-style-type:none;display:flex;justify-content:space-between}.FrontPage .bio .key-words>*:not(:first-child){margin-left:15px}}
.Projects{background-image:url(/portfolio/static/media/abstract-background-v2-blur.6ab33ef4.jpg);background-repeat:no-repeat;background-size:cover}.Projects .projects-list{position:relative;padding:80px 0;list-style:none;display:flex;justify-content:center;flex-wrap:wrap}.Projects .projects-list>li>*{padding:0;display:block;margin:10px}@media screen and (max-width: 640px){.Projects .projects-list{padding:30px 0}}
.ProjectCard{position:relative;height:500px;width:200px;padding:10px;border:3px solid rgba(255,255,255,.5);cursor:pointer;transition:.8s cubic-bezier(0.2, 0.61, 0.41, 0.84);transition-property:color,border-color,-webkit-filter;transition-property:filter,color,border-color;transition-property:filter,color,border-color,-webkit-filter}.ProjectCard.featured{border-color:#2affa2}.ProjectCard.featured:hover{border-color:#2affa2}.ProjectCard.featured:before{background-color:rgba(42,255,162,.2)}.ProjectCard.featured:active{color:rgba(42,255,162,.5);border-color:rgba(42,255,162,.5)}.ProjectCard.featured:active:before{background-color:rgba(42,255,162,.1)}.ProjectCard:before{content:" ";position:absolute;top:0;left:0;width:100%;height:0%;background-color:rgba(255,255,255,.2);transition:.8s cubic-bezier(0.2, 0.61, 0.41, 0.84);transition-property:height,width,background-color}.ProjectCard:hover{border-color:#fff}.ProjectCard:hover:before{height:100%}.ProjectCard:active{color:rgba(255,255,255,.5);border-color:rgba(255,255,255,.5)}.ProjectCard:active:before{background-color:rgba(255,255,255,.1)}.ProjectCard.alt-hover{-webkit-filter:grayscale(100%);filter:grayscale(100%)}.ProjectCard .project-container{position:relative;background-color:#1a1d22;box-shadow:inset 0 0 5px rgba(255,255,255,.2);height:100%;overflow:hidden}.ProjectCard .project-background{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);height:100%;width:auto}.ProjectCard .project-logo{position:absolute;width:100%;height:auto;top:50%;left:50%;transform:translate(-50%, -50%)}.ProjectCard .featured-text{font-family:"Bebas Neue",sans-serif;font-size:18px;letter-spacing:1px;background-color:#2affa2;color:#000;top:0;left:0;padding:5px 10px;z-index:1;position:absolute;pointer-events:none}@media screen and (max-width: 640px){.ProjectCard{box-sizing:border-box;height:220px;width:75vw}.ProjectCard .project-background{height:auto;width:100%}.ProjectCard .project-logo{width:240px;height:auto}}
.Project{position:relative}.Project .project-header{position:relative;padding-top:88px;height:640px;overflow:hidden}.Project .project-header .project-showcase{position:fixed;top:50%;left:50%;width:auto;height:auto;min-width:100vw;min-height:100vh;pointer-events:none;transform:translate(-50%, -50%)}.Project .project-header:after{content:" ";position:fixed;top:0;left:0;width:100vw;height:100vh;background-color:#000;z-index:0;opacity:.3}.Project .project-header .project-identity{z-index:1;position:absolute;top:50%;left:50%;margin-top:25px;transform:translate(-50%, -50%)}.Project .project-header .project-logo{display:block;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:500px;height:auto;pointer-events:none;margin:0 auto;transition:width .3s,height .3s,transform .3s}.Project .project-header .project-title{-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);border:4px solid #fff;padding:30px;text-align:center;font-size:84px;text-shadow:0 0 5px rgba(0,0,0,.5);transition:font-size .3s}.Project .portfolio-button{margin:30px;position:fixed !important;z-index:5;-webkit-backdrop-filter:blur(15px);backdrop-filter:blur(15px)}.Project .project-content{padding-bottom:40px}.Project .project-content .project-section{position:relative;z-index:1;border:3px solid rgba(255,255,255,.5);padding:10px;box-sizing:border-box;width:100%;max-width:960px;min-height:240px;overflow:hidden;margin:0 auto;margin-bottom:20px}.Project .project-content .project-section.full-text .section-description{margin:0 auto;position:relative}.Project .project-content .project-section.full-text .section-description:before{top:50%;left:50%;position:absolute;width:100vw;transform:translate(-50%, -50%);margin:none;background:linear-gradient(to left, rgba(113, 153, 217, 0.15) 0%, rgba(17, 17, 17, 0) 40%, rgba(17, 17, 17, 0) 60%, rgba(113, 153, 217, 0.15) 100%)}.Project .project-content .section-container{background-color:#111;z-index:-1;position:relative;overflow:hidden;white-space:normal;width:100%;min-height:240px}.Project .project-content .section-image{z-index:-1;position:absolute;height:100%;width:auto;-webkit-mask-image:linear-gradient(to right, black 50%, rgba(0, 0, 0, 0) 100%);mask-image:linear-gradient(to right, black 50%, rgba(0, 0, 0, 0) 100%);transform:translateX(-15%)}.Project .project-content .section-title{font-size:36px}.Project .project-content .section-description{position:relative;padding:15px;margin-left:auto;max-width:400px;min-height:240px}.Project .project-content .section-description:before{z-index:-1;position:absolute;content:" ";right:0;top:0;width:150%;height:100%;background:linear-gradient(to left, #111111 0%, rgba(17, 17, 17, 0) 100%)}.Project .project-content .section-text>*{white-space:normal;margin:15px 0;letter-spacing:.1px}@media screen and (max-width: 960px){.Project .project-header .project-identity{width:95%}.Project .project-header .project-title{font-size:64px}}@media screen and (max-width: 740px){.Project .section-description{-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px)}}@media screen and (max-width: 640px){.Project .project-header .project-title{font-size:56px}.Project .project-header .project-logo{width:400px}}
.App{position:relative}.App h1,.App h2{font-family:"Bebas Neue",sans-serif;font-weight:400;font-size:64px;letter-spacing:1px}.App .portfolio-button{position:relative;font-family:"Bebas Neue",sans-serif;font-size:24px;letter-spacing:1px;background-color:transparent;color:#fff;padding:5px 10px;border:3px solid #fff;cursor:pointer;transition:.4s cubic-bezier(0.2, 0.61, 0.41, 0.84);transition-property:color,border-color}.App .portfolio-button:before{content:" ";position:absolute;top:0;left:0;width:0%;height:100%;background-color:rgba(255,255,255,.5);transition:.4s cubic-bezier(0.2, 0.61, 0.41, 0.84);transition-property:width,background-color}.App .portfolio-button:hover:before{width:100%}.App .portfolio-button:active{color:rgba(255,255,255,.5);border-color:rgba(255,255,255,.5)}.App .portfolio-button:active:before{background-color:rgba(255,255,255,.25)}.App .portfolio-button:focus{outline:none}.App .portfolio-button-color{position:relative;font-family:"Bebas Neue",sans-serif;font-size:24px;letter-spacing:1px;background-color:transparent;color:#2affa2;padding:5px 10px;border:3px solid #2affa2;cursor:pointer;transition:.4s cubic-bezier(0.2, 0.61, 0.41, 0.84);transition-property:color,border-color}.App .portfolio-button-color:before{content:" ";position:absolute;top:0;left:0;width:0%;height:100%;background-color:rgba(42,255,162,.5);transition:.4s cubic-bezier(0.2, 0.61, 0.41, 0.84);transition-property:width,background-color}.App .portfolio-button-color:hover:before{width:100%}.App .portfolio-button-color:active{color:rgba(42,255,162,.5);border-color:rgba(42,255,162,.5)}.App .portfolio-button-color:active:before{background-color:rgba(42,255,162,.25)}.App .portfolio-button-color:focus{outline:none}.App .inline-bar{content:" ";display:inline-block;height:2px;background-color:#fff;vertical-align:middle;transform:translateY(-1px);margin:0 15px}.App .page-container{max-width:1280px;margin-left:auto;margin-right:auto;padding-left:10px;padding-right:10px}
